* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main-container{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  min-height: 100vh;
  max-height: 100%;
  width: 100vw;
}
.nav-bar  {
  width: 100vw;
  height: 70px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #2c0e3a;
  padding: 0px 5%;
}
.top-image {
  width: 130px;
  height: 30px;
}

.score-div {
  display: flex;
  flex-direction: row;
  justify-self: flex-start;
  align-items: center;
  list-style-type: none;
}
.score-name {
  color: #fff;
  font-size: 15px;
  margin-right: 5px;
}
.score {
  color: #fec653;
  font-size: 18px;
  font-weight: bold;
  margin-right: 20px;
}
.timer-img{
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.time{
  color: #fec653;
  font-size: 18px;
  margin-right: 20px;
  font-weight: bold;
}
.content-div {
  width: 100vw;
  height: 100%;
  background-image: url('https://assets.ccbp.in/frontend/react-js/match-game-bg.png');
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 40px;
}
.fist-div {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

}
.big-image {
  width: 500px;
  height: 300px;
}
.tab-element {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  list-style-type: none;
}

.tab-button {
  background-color: transparent;
  margin-top: 10px;
  margin-bottom: 10px;
  border-style: none;
  color: #fff;
  font-size: 20px;
  outline: none;
  min-width: 60px;
  height: 40px;
  margin-right: 20px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.highlight-text{
  color: #fec653;
  border-bottom: solid;
  border-bottom-color: #fec653;

}
.thumbnail-images{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  width: 500px;
  list-style-type: none;
  padding: 0px;
}

.image-button {
  width: 86px;
  height: 86px;
  background-color: transparent;
  border-style: none;
  outline: none;
  margin-right: 14px;
  margin-bottom: 14px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.thumbnail-image {
  width: 86px;
  height: 86px;
}
.second-div{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 480px;
  height: 600px;
  background-image: url('https://assets.ccbp.in/frontend/react-js/match-game-score-card-lg-bg.png');
  background-size: cover;

}
.trophy-image {
  width: 280px;
  height: 220px;
  margin-top: 100px;
}
.main-heading {
  color: #fff;
  font-size: 18px;
  margin: 0px;

}
.your-score {
  columns: white;
  font-size: 34px;
  margin: 10px;
  margin-bottom: 0px;
}
.play-button{
  width: 160px;
  padding: 5px;
  border-style: none;
  outline: none;
  background-color: #cf60c8;
  border-radius: 10px;
  font-size: 16px;
  color: white;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.restart{
  width: 15px;
  height: 15px;
  margin-right: 10px;
}

@media screen and (max-width:576px) {
  body{
    overflow-x: hidden;
    overflow-y: hidden;
  }
  .nav-bar{
    height: 45px;
  }
  .top-image{
    width: 100px;
    height: 25px;
  }
  .score-name{
    font-size: 12px;
  }
  .score{
    font-size: 14px;
  }
  .timer-img{
    width: 12px;
    height: 12px;
  }
  .time{
    font-size: 14px;
    margin-right: 10px;
  }
  .big-image{
    width: 300px;
    height: 200px;
  }
  .content-div{
    padding: 20px;
    height: 95vh;
    min-height: 100%;
    overflow-y: hidden;
  }
  .tab-button{
    min-width: 40px;
    height: 30px;
    font-size: 14px;
  }
  .thumbnail-images{
    width: 300px;
    height: auto;
    justify-content: center;
  }
  .image-button{
    width: 60px;
    height: 60px;
  }
  .thumbnail-image{
    width: 60px;
    height: 60px;
  }

  .second-div{
    width:95%;
    height: 70%;
    background-image: url('https://assets.ccbp.in/frontend/react-js/match-game-score-card-sm-bg.png');
    padding: 40px;
    margin-top: 40px;
  
  }
  .trophy-image {
    width: 220px;
    height: 180px;
    margin-top: 82px;
  }
  .main-heading {
    font-size: 14px;
  }
  .your-score {
    font-size: 32px;
  }
  .play-button{
    width: 120px;
    font-size: 12px;
  }
  
}